import { getStoreDetail, getStoreInfo } from '@/api/biunique'
import config from "@/config/common.config"
import BackgroundPromoteBottom from '@/components/backgroundPromote/BackgroundPromoteBottom'
import { storage } from 'ais-common-utils'
export default {
  components:{
    BackgroundPromoteBottom
  },
  async asyncData (context) {
    const { data } = await getStoreDetail(context.route.params.id)
    if (data.code === 0) {
      return {
        data: {
          ...data.data,
          id:context.route.params.id
        }
      }
    }
  },
  data() {
    return {
      data:{},
      config
    }
  },
  mounted() {
    this.getStoreInfo()
  },
  methods:{
    async getStoreInfo() {
      const { data } = await getStoreInfo(this.$route.params.id)
      if(data.code === 0) {
        this.$set(this.data, 'apply', data.data.apply)
      }
    },
    handleClick() {
      let path = ''
      if(this.data.type === 1) {
        path = `/promoteApply/${this.data.id}`
      }else if(this.data.type === 2) {
        if(this.data.price === 0) {
          path = `/promoteQuestion/${this.data.id}`
        } else {
          path = `/promoteOrder/${this.data.id}`
        }
      }
      if( !storage.get('userStatus')){
        const url = encodeURIComponent(`${config.LINK.school_h5}${path}`)
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
        return
      }
      this.$router.push({
        path:path
      })
    }
  },
  head () {
    return {
      title: '科研背景提升课程详情—艾思云课堂',
    }
  }
}
